import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import About from "./views/About.vue";
import hello2 from "./views/hello.vue";
import Blog from "./views/Blog.vue";

Vue.use(Router);

const routes= [
  {
    path: "/",
    name: "home",
    component: Home
  },
  {
    path: "/about",
    name: "about",
    component: About
   
  },
  {
    path: "/blog",
    name: "blog",
    component: Blog
   
  },
  {
  path:'/hello',
  name:'hello4',
  component: hello2
  }
]

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});
